<template>
	<div class="login-container">
		<div class="login-header">
			<p>重置密码</p>
		</div>
		<div class="form-box">
			<Form ref="formData" :model="formValidate" :rules="ruleValidate">
				<FormItem prop="phone">
					<i-input v-model="formValidate.phone" clearable size="large" placeholder="请输入手机号">
						<Icon type="md-phone-portrait" slot="prepend"></Icon>
					</i-input>
				</FormItem>
				<FormItem prop="checkNum">
					<i-input v-model="formValidate.checkNum" size="large" placeholder="请输入验证码">
						<Icon type="md-checkmark-circle-outline" slot="prepend"></Icon>
						<Button v-if="checkNumSent" slot="append" disabled>重新发送 ({{time}}s)</Button>
						<Button v-else slot="append" @click="getCheckNum">获取验证码</Button>
					</i-input>
				</FormItem>
				<FormItem prop="password">
					<i-input type="password" v-model="formValidate.password" clearable size="large" placeholder="请输入你的密码">
						<Icon type="md-lock" slot="prepend"></Icon>
					</i-input>
				</FormItem>
				<FormItem prop="rePassword">
					<i-input type="password" v-model="formValidate.rePassword" clearable size="large" placeholder="请再次输入你的密码">
						<Icon type="md-unlock" slot="prepend"></Icon>
					</i-input>
				</FormItem>
				<Button type="error" size="large" long @click="handleSubmit">重置密码</Button>
			</Form>
		</div>
	</div>
</template>

<script>
	import {mapActions, mapMutations} from 'vuex';

	export default {
		name: 'RetrievePassword',

		data() {
			const validatePassCheck = (rule, value, callback) => {
				if (value !== this.formValidate.password) {
					callback(new Error('两次输入的密码不一样'));
				} else {
					callback();
				}
			};

			return {
				formValidate: {
					phone: '',
					checkNum: '',
					password: '',
					rePassword: '',
				},

				ruleValidate: {
					phone: [
						{required: true, message: '手机号不能为空', trigger: 'blur'},
						{type: 'string', pattern: /^1[3|4|5|7|8|9][0-9]{9}$/, message: '手机号格式出错', trigger: 'blur'}
					],
					checkNum: [
						{required: true, message: '必须填写验证码', trigger: 'blur'},
						{type: 'string', min: 6, max: 6, message: '验证码长度错误', trigger: 'blur'}
					],
					password: [
						{required: true, message: '密码不能为空', trigger: 'blur'},
						{type: 'string', min: 6, message: '密码长度不能小于6', trigger: 'blur'}
					],
					rePassword: [
						{required: true, message: '请再次输入密码', trigger: 'blur'},
						{validator: validatePassCheck, trigger: 'blur'}
					]
				},

				checkNumSent: false,
				time: 60,
			}
		},

		mounted() {},

		methods: {
			countDown() {
				if (this.time > 0) {
					setTimeout(() => {
						this.time--;
						this.countDown();
					}, 1000);
				} else {
					this.time = 60;
					this.checkNumSent = false;
				}
			},

			async getCheckNum() {
				if (this.formValidate.phone.length === 11) {
					let res = await this.$request.sendSms(this.formValidate.phone);
					if (res) {
						this.$Message.success({
							content: '验证码已发送',
							duration: 3,
							closable: true
						});
						this.checkNumSent = true;
						this.countDown();
					} else {
						this.$Message.error({
							content: '验证码发送失败',
							duration: 3,
							closable: true
						});
					}
				} else {
					this.$Message.error({
						content: '请输入正确的手机号',
						duration: 3,
						closable: true
					});
				}
			},

			handleSubmit() {
				this.$refs['formData'].validate(async (valid) => {
					if (valid) {
						let res = await this.$request.checkCode(this.formValidate.phone, this.formValidate.checkNum);
						if (res) {
							let lres = await this.$request.retrievePassword(this.formValidate.phone, this.formValidate.password);
							if (lres) {
								this.$Message.success('重置密码成功');
								this.$router.push('/login');
							} else {
								this.$Message.error('重置密码失败，请重试！');
							}
						} else {
							this.$Message.error({
								content: '验证码错误',
								duration: 3,
								closable: true
							});
						}
					} else {
						this.$Message.error({
							content: '请填写正确的信息',
							duration: 3,
							closable: true
						});
					}
				});
			}
		},
	};
</script>

<style scoped>
	.login-container {
		width: 80%;
		height: 400px;
		border: #ED3F14 solid 1px;
	}

	.login-header {
		height: 50px;
		font-size: 20px;
		text-align: center;
		line-height: 50px;
		letter-spacing: 5px;
		color: #fff;
		background-color: #ED3F14;
	}

	.form-box {
		width: 80%;
		margin: 30px auto;
	}
</style>



